$cell-width: 50px;
$task-row-height: 50px;
$cell-height: $task-row-height;
$drag-width: 20px;
$border-color: #aaa;
$title-background: lightgray;
$title-padding-left: 2px;
$title-min-width: 170px;

.cell-actions {
    position: relative;
    left: -$cell-width;
    width: calc($cell-width * 3);
    display: flex;
    z-index: 1000;

    .action-plus {
        background-color: lightgray;
    }

    .action-minus {
        background-color: lightgray;
    }

    .cell-action {
        width: $cell-width;
        height: $task-row-height;

        button {
            border: 1px solid black;
            text-align: center;
            width: $cell-width;
            height: $task-row-height;

            &.hover {
                font-weight: bold;
                background-color: #aaa;
            }
        }
    }
}


.task-table {

    .stat-cell-head-title {
        width: $cell-width;
        height: $cell-height;
        box-sizing: border-box;
        border-bottom: 1px solid $border-color;
        color: $border-color;
        background: white;
        line-height: $cell-height;
        vertical-align: middle;
        text-align: center;
    }

    .stat-cell {
        width: $cell-width;
        height: $cell-height;
        box-sizing: border-box;
        border-bottom: 1px solid $border-color;
        color: $border-color;
        background: white;
        line-height: $cell-height;
        vertical-align: middle;
        text-align: center;
        font-size: 80%;
    }

    .tasks {
        .sortable>:last-child .stat-cell {
            border-bottom: none;
        }
    }

    .task-row {

        .drag-handler {
            width: $drag-width;
            transform: translate(0, 50%);
        }

    }

    .table-head {
        display: flex;
        background: white;
        user-select: none;
    }

    .context-menu-is-showing .date-cell {
        background-color: lightblue;
    }


    .date-cell {
        height: $task-row-height;
    }

    .cell {
        width: $cell-width;
    }

    .task-title-column {
        min-width: $title-min-width;
        border-right: 0.5px solid $border-color;
        border-bottom: 0.5px solid $border-color;
        box-sizing: border-box;
    }

    .date-cell {
        border-right: 0.5px solid $border-color;
        border-bottom: 0.5px solid $border-color;
        box-sizing: border-box;
        background-color: lightgray;
        text-align: center;
        width: $cell-width;
        min-width: $cell-width;
    }

    .days-wrapper {
        .days {
            display: flex;
        }
    }

    .task-row {
        display: flex;
        padding: 0;
        margin: 0;
        height: $task-row-height;
        background: white;
        user-select: none;

        .task-title-column {
            display: flex;
            box-sizing: border-box;
        }

        .title {
            height: 100%;
            width: 100%;

            input {
                height: 100%;
                width: 100%;
                background: $title-background;
                padding-left: $title-padding-left;
            }

            border-left: 0.5px solid $border-color;
        }

        .points {
            height: 100%;
            display: flex;

            input {
                width: 25px;
                height: 100%;
                background: $title-background;
                font-size: 10px;
                text-align: center;
            }
        }

        .measures {
            height: 100%;
            display: flex;
        }

        .cell {
            height: 100%;
            box-sizing: border-box;
            border-right: 0.5px solid $border-color;
            border-bottom: 0.5px solid $border-color;

            input {
                width: 100%;
                height: 100%;
                text-align: center;
            }
        }

        .cell.gray {
            background: lightgray;
        }

        .cell.green {
            background: lightgreen;
        }

        .cell.red {
            background: lightpink;
        }
    }

    .table-footer {
        .task-title-column {
            height: $task-row-height;
            display: flex;

            .margin {
                display: inline-block;
                width: $drag-width;
                box-sizing: border-box;
                height: $task-row-height;
            }

            .title {
                display: inline-block;
                width: calc(100% - $drag-width);
                height: 100%;
                box-sizing: border-box;

                input {
                    width: 100%;
                    height: 100%;
                    padding-left: $title-padding-left;

                    &::placeholder {
                        color: $border-color;
                    }
                }

                border-left: 0.5px solid $border-color;
                border-image: linear-gradient(to bottom, $border-color, rgba(0, 0, 0, 0)) 1 100%;
            }

            border-right: none;
            border-bottom: none;
        }
    }
}


.stat-cell-head-title {
    position: relative;

    button.custom-button.current-stat-title {
        position: absolute;
        left: 0;
        font-size: 80%;
        width: $cell-width;
        height: $cell-height;
        text-align: center;
    }
}

.change-stat-overlay {
    position: relative;
    left: calc(-2 * $cell-width - 1px);
    width: calc($cell-width * 3);
    display: flex;
    z-index: 1000;

    .action-button {
        min-width: $cell-width;
        width: $cell-width;
        min-height: $cell-height;
        height: $cell-height;
        box-sizing: border-box;
        border-left: 1px solid $border-color;
        border-bottom: 1px solid $border-color;
        background: white;
        font-size: 80%;

        &.hover {
            font-weight: bold;
            background-color: #aaa;
        }
    }
}